<template>

  <div>
    <form class="loginPage">
      <div class="logoContainer">
        <img :src="logoPath"/>
      </div>
      <div class="container">
        <span class="item">Username: </span>
        <input v-model="username" type="text" class="item"/>
        <span class="item">Password: </span>
        <input v-model="pwd" type="password" class="item"/>

        <button type="button" v-on:click="login" class="loginButton">Log in</button>
        <router-link to="/register">Register</router-link>
        <b-alert class="item" v-model="error" variant="danger">{{message}}</b-alert>
      </div>
      <div class="container"></div>
    </form>
  </div>

</template>

<script>
import { RouterLink } from 'vue-router';


export default {
  name: 'Login_page',
  data() {
    return {
      username: '',
      pwd: '',
      logoPath: require('@/assets/logo.png'),
      error: false,
      message: ''
    }
  },
  methods: {
    login: async function () {
      const response = await this.$store.dispatch("login",{
        "username": this.username,
        "pwd": this.pwd,
      });
      
      if(response.status != 200){
        this.message = response.data;
      }
      else{
        this.$router.push('/home');
      }
    },
  },
  components: {
    'router-link': RouterLink,
  }
}
</script>

